erdana, sans-serif;\n  line-height: 1.5;\n  background: radial-gradient(\n    circle at center,\n    #3f3f3f 0%,\n    #2f2f2f 40%,\n    #1e1e1e 100%\n  );\n  -webkit-font-smoothing: antialiased;\n\n  /* Miscellaneous / Interactions */\n  box-shadow: inset 0 0 7.5rem rgba(0, 0, 0, 0.4);\n}\n\n/* Text / Font */\n\np {\n  color: var(--text-primary);\n  font-size: 1.25rem;\n}\n\n@font-face {\n  font-family: Progress;\n  src: url(/src/fonts/PROGRESS\\ PERSONAL\\ USE.ttf);\n}\n\n/* Logo / Info Panel */\n\n.logo-text {\n  /* Box Model */\n  margin: 0.625rem;\n\n  /* Typography / Visuals */\n  font-size: 5rem;\n  font-weight: 500;\n  letter-spacing: 0.125rem;\n  color: var(--text-logo);\n\n  /* Miscellaneous / Interactions */\n  text-shadow:\n    0 -1px 0 var(--shadow-1),\n    0 1px 0 var(--shadow-2),\n    0 2px 0 var(--shadow-3),\n    0 3px 0 var(--shadow-4),\n    0 4px 0 var(--shadow-5),\n    0 5px 0 var(--shadow-6),\n    0 6px 0 var(--shadow-7),\n    0 7px 0 var(--shadow-8),\n    0 8px 0 var(--shadow-9),\n    0 9px 0 var(--shadow-10),\n    0 10px 0 var(--shadow-11),\n    0 11px 0 var(--shadow-12),\n    0 12px 0 var(--shadow-13),\n    0 13px 0 var(--shadow-14),\n    0 14px 0 var(--shadow-15),\n    0 15px 0 var(--shadow-16),\n    0 1.375rem 1.875rem var(--rgba-black-darkest);\n}\n\n.info-container {\n  /* Box Model */\n  position: relative;\n  width: 28%;\n  padding: 0.5rem 2rem;\n  border-radius: 0.1875rem;\n  border: 1px solid var(--border-dark);\n\n  /* Typography / Visuals */\n  text-align: center;\n  font-size: 0.875rem;\n  color: var(--text-primary);\n  letter-spacing: 0.0625rem;\n  background:\n    radial-gradient(\n      ellipse at center,\n      var(--rgba-white-light) 0%,\n      var(--rgba-white-lighter) 40%,\n      var(--rgba-black-medium) 100%\n    ),\n    linear-gradient(\n      to bottom,\n      var(--bg-dark-light) 0%,\n      var(--bg-dark-medium) 50%,\n      var(--bg-dark-heavy) 100%\n    );\n\n  /* Miscellaneous / Interactions */\n  box-shadow:\n    inset 0 1px 0 var(--rgba-white-heavy),\n    inset 0 -1px 0 var(--rgba-black-heavy),\n    0 2px 4px var(--rgba-black-heavy);\n}\n\n.info-container::before,\n.info-container::after {\n  /* Box Model */\n  content: '';\n  position: absolute;\n  width: 0.5rem;\n  height: 0.5rem;\n  border-radius: 50%;\n  top: 50%;\n  transform: translateY(-50%);\n\n  /* Typography / Visuals */\n  background: radial-gradient(\n    circle,\n    var(--gradient-accent-1) 0%,\n    var(--gradient-accent-2) 70%\n  );\n}\n\n.info-container::before {\n  left: 0.75rem;\n}\n\n.info-container::after {\n  right: 0.75rem;\n}\n\n/* Game Boards */\n\n.boards-container {\n  display: flex;\n  column-gap: 3rem;\n}\n\n.outter-board-wrap {\n  /* Box Model */\n  position: relative;\n  padding: 0.5625rem;\n  border-radius: 0.25rem;\n\n  /* Miscellaneous / Interactions */\n  transition: background-position 0.3s ease;\n}\n\n.outter-board-wrap:hover {\n  background-position: center right;\n}\n\n.inner-board-wrap {\n  /* Box Model */\n  position: relative;\n  padding: 0.25rem;\n\n  /* Typography / Visuals */\n  background: linear-gradient(\n    to right,\n    var(--accent-blue-1) 0%,\n    var(--accent-blue-2) 60%\n  );\n}\n\n.inner-board-wrap.enemy {\n  /* Visuals */\n  background: linear-gradient(\n    to right,\n    var(--accent-purple),\n    var(--accent-red)\n  );\n}\n\n.human-section,\n.cpu-section {\n  display: grid;\n  place-items: center;\n  row-gap: 0.75rem;\n}\n\n.human-board,\n.cpu-board {\n  display: grid;\n\n  /* Box Model */\n  width: 348px;\n  height: 348px;\n}\n\n.row {\n  display: flex;\n  flex-direction: row;\n  flex: 1;\n}\n\n/* Cells */\n\n.cell {\n  /* Grid / Flex Layout */\n  display: flex;\n  flex: 1;\n\n  /* Box Model */\n  position: relative;\n  border-right: 2px solid var(--border-light);\n  border-bottom: 2px solid var(--border-light);\n\n  /* Typography / Visuals */\n  background-color: var(--bg-board);\n}\n\n.row .cell:nth-child(10) {\n  border-right: 0px;\n}\n\n.row:nth-child(10) .cell {\n  border-bottom: 0px;\n}\n\n.cpu-board .cell:hover {\n  cursor: crosshair;\n}\n\n.human-board .cell:hover {\n  cursor: not-allowed;\n}\n\n.cell > img {\n  /* Box Model */\n  position: absolute;\n  width: 60%;\n\n  /* Miscellaneous / Interactions */\n  top: 50%;\n  left: 50%;\n  transform: translate(-50%, -50%);\n}\n\n.cell.ship:not(.sunk) {\n  background-image: linear-gradient(\n    to right,\n    var(--gradient-shine) 0%,\n    var(--gradient-bright) 51%,\n    var(--gradient-shine) 100%\n  );\n  background-size: 200% auto;\n}\n\n.cell.ship.sunk {\n  background-color: var(--bg-sunk-ship);\n}\n\n/* Buttons */\n\n.buttons {\n  justify-self: stretch;\n\n  /* Grid / Flex Layout */\n  display: grid;\n  place-items: center;\n  grid-template-rows: max-content max-content;\n  row-gap: 1.25rem;\n\n  /* Typography / Visuals */\n  font-size: 12px;\n}\n\n.down-buttons {\n  display: grid;\n  grid-template-columns: max-content max-content;\n  column-gap: 3rem;\n}\n\n.buttons button {\n  /* Box Model */\n  padding: 0.25rem 1rem;\n  width: 11.25rem;\n  min-width: max-content;\n  border-radius: 0.1875rem;\n\n  /* Typography / Visuals */\n  color: var(--text-primary);\n  letter-spacing: 0.0625rem;\n  text-align: center;\n}\n\n.buttons button:hover {\n  /* Typography / Visuals */\n  color: var(--text-light);\n\n  /* Miscellaneous / Interactions */\n  background-position: right center;\n}\n\n.buttons button:active {\n  box-shadow: none;\n}\n\n/* Utilities */\n\n.glow-animation {\n  /* Box Model */\n  border: 1px solid var(--border-dark);\n\n  /* Typography / Visuals */\n  background-image: linear-gradient(\n    to right,\n    var(--gradient-shine) 0%,\n    var(--gradient-bright) 51%,\n    var(--gradient-shine) 100%\n  );\n\n  /* Miscellaneous / Interactions */\n  transition: 0.5s;\n  background-size: 200% auto;\n  box-shadow:\n    inset 0 1px 0 var(--rgba-white-heavy),\n    inset 0 -1px 0 var(--rgba-black-heavy),\n    0 2px 4px var(--rgba-black-heavy);\n}\n"],"sourceRoot":""}]);
